const pricingInverse = {};
pricing.forEach(
  cloth => cloth.categories.forEach(
    category => category.items.forEach(
      (item) => {
        pricingInverse[item.id] = {
          ...item,
          cloth: cloth.cloth,
          category: category.name,
        };
      },
    ),
  ),
);

export { pricingInverse };



